@use '../abstracts' as a;
@use '../functions/' as *;


.hero {
    &__top {
        background: #24768C;
        display: flex;
        justify-content: center;
        position: relative;
        #avengers {
            width: 100%;
            position: relative;
            top: 15px;
            z-index: 1;
        }
        &::before {
            content: "";
            background: url("../img/Hero-ray.svg");
            background-size: cover;
            position: absolute;
            width: 100%;
            height: 100%;
        }
        &::after {
            position: absolute;
            content: "";
            background: url("../img/Hero-city.svg");
            background-size: cover;
            background-repeat: no-repeat;
            background-position-x: center;
            width: 100%;
            height: 100%;

        }
    }
    &__bottom {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: a.$marvel-red;
        font-family: a.$primary-font;
        font-size: 1.7rem;
        color: a.$secondary-font-color;
        height: rem(50);
    }
}


